@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&family=Oswald:wght@400;500&family=Pacifico&family=Pridi&display=swap);
/*Fuentes Google Fonts*/

*
{
    margin: 0;
    padding: 0;
}

body
{
    background-color: #D9CDBF;
}

h1
{
    font-family: 'Oswald', sans-serif;
}

p
{
    font-family: 'Open Sans', sans-serif;
}
.list{
    list-style: none;
    display: block;
    text-align: center;
    padding-top: 16px;
    padding-bottom: 16px;
}

.opcion{
    color: white;
    text-decoration: none;
    font-size: 1.2em;
    font-family: 'Pridi', serif;
}

.opcion:hover{
    text-decoration: none;
    color:white;
    background: #A7D4D9;
    text-shadow: 2px 2px 2px #0C1F26;
}
.menu{ 
    background-color: #0C1F26;
    box-shadow: 2px 2px 2px 2px gray;
}

.logo{
    font-family: 'Pacifico', cursive;
    text-shadow: 1px 1px 1px gray;
    color: white;
    font-size: 2em;
    text-decoration: none;
}

.nombre{
    margin-left: 30px;
    padding-top: 6px;
    padding-bottom: 6px;
    -webkit-transition: all 0.75s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.logo:hover{
    color: white;
    text-decoration: none;
}
